.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(5deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    color: var(--color-white);
    border: 0.5px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: var(--color-white);
    color: var(--color-bg-);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__card a:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: var(--color-prymary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.3rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.8rem;
}

.about__card p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* ============== MEDIA QUERTES (MEDIUM DEVICE) ============== */
@media screen and (max-width: 1024px) {
    
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4ren;
    }

    .about__content{
        text-align: center;
    }
 }

/* ============== MEDIA QUERTES (SMALL DEVICE) ============== */
@media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }
  }